import BaseIframe from "../../plugin/script/common/BaseIframe";
import InputArea from "../../plugin/components/InputArea";
import PermissionButton from "../../plugin/components/PermissionButton";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "Sales",
  extends: BaseIframe,
  components: { InputArea, PermissionButton, InfoTable, InfoTableItem },
  data() {
    return {
      //表格数据
      tableStyle: [
        { label: "代理级别", prop: "min_result", width: "" },
        { label: "最小业绩", prop: "max_result", width: "" },
        { label: "最大业绩", prop: "max_result", width: "" },
        { label: "业绩返佣比例", prop: "rate_result", width: "" },
        { label: "操作", prop: "action", width: "" }
      ],
      records: [
        {
          min_result: "1.00",
          max_result: "10000.00",
          rate_result: "1",
          action: [
            { label: "修改", type: "edit" },
            { label: "删除", type: "del" }
          ]
        }
      ],
      pageInfo: new PageInfo(0, [5, 10, 15], 0), // page pageSizes total
      //弹窗数据
      dialogTitle: "",
      dialogVisible: false,
      labelWidth: "70px",
      dataForm: {
        agent_class: "",
        min_pre: "0",
        max_pre: "10000.00",
        column: "1"
      }
    };
  },
  methods: {
    search() {},
    //打开新增的弹窗方法
    handelAddClick() {
      this.dialogVisible = true;
      this.dialogTitle = "业绩返佣新增";
    },
    //表单操作
    // handelClick
    handelClick(btn, row) {
      this.dialogVisible = true;
      this.dialogTitle = "业绩返佣修改";
    },
    //新增方法
    confirm() {
      // 通过层级id是否为真来判断是新增还是修改
    }
  }
};



