import InfoTable from "../../plugin/components/InfoTable";
import PermissionButton from "../../plugin/components/PermissionButton";
import InputArea from "../../plugin/components/InputArea";
import SelectTime from "../../plugin/components/SelectTime";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "SalesList",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InfoTable,
    PermissionButton,
    InputArea,
    SelectTime
  },
  data() {
    return {
      format:{
        platform:'',
      },
      platforms: [
        { label: "平台一", value: "1" },
        { label: "平台二", value: "2" }
      ],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      date:[],
      tableStyle: [
        { label: "时间", prop: "time", width: "" },
        { label: "用户ID", prop: "user_id", width: "" },
        { label: "用户昵称", prop: "user_name", width: "" },
        { label: "所属渠道", prop: "team_result", width: "" },
        { label: "团队人数", prop: "ratio", width: "" },
        { label: "直属下级人数", prop: "team_ratio", width: "" },
        { label: "直属下级流水", prop: "obtain_ratio", width: "" },
        { label: "团队流水", prop: "obtain_ratio", width: "" },
        { label: "直属佣金", prop: "obtain_ratio", width: "" },
        { label: "代理佣金", prop: "obtain_ratio", width: "" },
        { label: "操作", prop: "action", width: "" },
      ],
      //表数据
      records: [
        {
          time: "2019-09-01 12:00:00",
          user_id: "1001100",
          user_name: "武广",
          team_result: "1000000.00",
          ratio: "6",
          team_ratio: "600.00",
          obtain_ratio: "100.00",
          action: ""
        }
      ],
      pageInfo: new PageInfo(0, [5, 10, 15], 0)
    };
  },
  methods: {
    search() {}
  }
};



